import React, { useState } from 'react';
import styles from './Transfer.module.scss';
import info from '../../../img/UI/info.svg';
import InputField from '../../../components/InputField/InputField';
import LinkBack from '../../../components/LinkBack/LinkBack';
import { TRANSFERS_ROUTE } from '../../../utils/consts';
import Success from '../../../components/Success/Success';
import { topup } from './../../../API/transactionsAPI';
import { checkIfNumberIsMoney, validateMoney } from '../../../utils/validate';
import userStore from '../../../store/UserStore';

const TopUp = (() => {
  const [money, setMoney] = useState('');
  const [isShow, setIsShow] = useState(false);

  const topupMoney = async (e) => {
    e.preventDefault();
    try { 
      const data = await topup(money);
      userStore.setBalance(data.balance)
      setIsShow(true);
    } catch(e) {
      console.log('[+] my error' + e);
    }
  }

  return (
    <>
      <Success isShow={isShow} setShow={setIsShow} 
        title='Your account has been successfully topped up for' 
        message={`$ ${money}`}
        btn='Top up again'/>
      <LinkBack title='Between my accounts' path={TRANSFERS_ROUTE}/>
      <form className={styles.transfer}>
        <div className={styles.transfer__header}>
          <h1 className={styles.transfer__title}>Top up account</h1>
          <img src={info} alt="info" />
        </div>
        <div className={styles.transfer__field}>
          <p className={styles.transfer__subtitle}>Amount</p>
          <InputField placeholder='0.00$' inputType='number' titleName='Enter the amount' 
            step=".01"
            onChange={(e) => {
              validateMoney(e.target);
              setMoney(e.target.value);
            }}
            field={styles.transfer__input} title={styles.transfer__inputTitle} 
            required />
        </div>
        <div className={styles.transfer__btns}>
          <button disabled={money === '' || !checkIfNumberIsMoney(money) ? true : false} 
            className={`btn ${styles.transfer__btn}`} 
            onClick={topupMoney}>Top up</button>
          <button className={`btn ${styles.transfer__btn} ${styles.transfer__btn_cancel}`}>Cancel</button>
        </div>
      </form>
    </>
  );
});

export default TopUp;