import React from 'react';
import './Success.scss';
import success from '../../img/UI/success.svg';
import { useNavigate } from 'react-router-dom';
import { OWERVIEW_ROUTE } from '../../utils/consts';

const Success = ({isShow, setShow, title, message, btn}) => {
  let navigate = useNavigate();
  return (
    <>
      { isShow &&
        <div className='success'>
          <div className='success__header'>
            <div className='success__iconContainer'>
              <img className='success__icon'src={success} alt="success" />
            </div>
          </div>
          <div className='success__content'>
            <div className='success__title'>{title} <div className='success__title-black'>{message}</div></div>
          </div>
          <div className='success__btns'>
            <button className='btn success__btn' onClick={() => setShow(false)}>{btn}</button>
            <button className='btn success__btn success__btn-back' onClick={() => navigate(OWERVIEW_ROUTE)}>Back to the main page </button>
          </div>
        </div>
      }    
    </>
  );
};

export default Success;